.discount-timer {
	// fix design bugs
	position: relative;
	left: dpx(-28);
	padding-bottom: dpx(3);
	// end - fix design bugs

	display: flex;
	// padding-right: dpx(57);

	&__label {
		// fix design bugs
		position: relative;
		left: dpx(-4);
		// end - fix design bugs

		display: flex;
		align-items: center;
		font-weight: 700;
		font-size: dpx(30);
		line-height: 130%;
		color: $color-theme-main-text;

		@include devices {
			font-weight: 500;
			font-size: dpx(16);
		}
	}

	&__clock-face {
		display: flex;
		margin-left: dpx(14);

		@include devices {
			margin-left: dpx(10.5);
		}
	}

	&__digit-number,
	&__digit-separator {
		// font-family: $font-bebas-neue;
		font-family: $font-bebas-neue-cyrillic;
		font-weight: 400;
		font-size: dpx(60);
		line-height: 103%;
		text-transform: uppercase;
		text-align: center;
		color: $color-theme-timer-number;

		@include devices {
			font-size: dpx(40);
		}
	}

	&__digit-number {
		padding: dpx(3) dpx(7.5) dpx(1);

		@include devices {
			padding: dpx(2) dpx(15) dpx(1);
		}
	}

	&__digit-separator {
		display: flex;
		align-items: center;
		justify-content: center;
		width: dpx(13);
		overflow: hidden;

		svg {
			// fix design bugs
			position: relative;
			margin-top: dpx(-15);
			// end - fix design bugs

			width: dpx(5);
			height: dpx(15);
			animation: blinker 1s linear infinite;

			@keyframes blinker {
				50% {
					opacity: 0;
				}
			}

			@include devices {
				width: dpx(4);
				height: dpx(14);
			}
		}

		@include devices {
			padding-top: dpx(10);
		}
	}

	&__digit-caption {
		// fix design bugs
		position: relative;
		margin-top: dpx(-10);
		// end - fix design bugs

		font-weight: 700;
		font-size: dpx(16);
		text-align: center;
		line-height: 130%;
		color: $color-theme-timer-label;

		@include devices {
			margin-top: dpx(-3);
			font-weight: 500;
			font-size: dpx(14);
		}
	}

	@include devices {
		left: 0;
	}
}

.js-discount-timer--is-ending {
	.discount-timer {
		&__digit-number,
		&__digit-separator {
			color: $color-theme-timer-active-discount;

			animation: 1.2s linear 0s infinite timer-blink;
		}

		@keyframes timer-blink {
			50% {
				opacity: 0.2;
			}
		}
	}
}
