.popup {
	&__mask {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1;
		background-color: rgba(0,0,0, 0.8);
		opacity: 0;
		visibility: hidden;
		overflow-y: auto;
		overflow-x: hidden;
		transition: all 0.8s ease;

		&--open {
			opacity: 1;
			visibility: visible;

			.popup__body {
				transform: translate(0%, 0%) rotate(0deg);
				opacity: 1;
			}
		}
	}

	&__wrap {
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 100%;
		padding: dpx(15);

		@include devices {
			padding: dpx(20);
		}
	}

	&__body {
		position: relative;
		width: dpx(750);
		// height: dpx(658);
		// max-width: dpx(750);
		background-color: #fff;
		transition: all .8s ease;
		transform: translate(-100%, -100%) rotate(45deg);
		overflow: hidden;
		opacity: 0;

		&-img {
			padding: 0;
		}
	}

	&__close {
		position: absolute;
		top: dpx(20);
		right: dpx(20);
		z-index: 2;
		width: dpx(24);
		height: dpx(24);
		cursor: pointer;

		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			width: dpx(20);
			height: 2px;
			background-color: $color-theme-popup-close;

			@include devices {
				width: dpx(14);
				height: 1px;
			}
		}

		&::before {
			transform: translate(-50%, -50%) rotate(45deg);
		}

		&::after {
			transform: translate(-50%, -50%) rotate(-45deg);
		}

		@include devices {
			top: dpx(10);
			right: dpx(10);
			width: dpx(20);
			height: dpx(20);
		}
	}

	&__content {
		padding: dpx(62) dpx(40) dpx(40);

		@include devices {
			padding: dpx(51) dpx(20) dpx(20);
			padding: dpx(51) dpx(19) dpx(20);
		}
	}

	&__header {
		min-height: dpx(50);
		background-color: darkgrey;
		padding: dpx(14) dpx(32) dpx(14) dpx(22);
	}

	&__mark {
		position: absolute;
		top: 0;
		left: dpx(42);
		padding: dpx(4) dpx(10) dpx(6);
		font-weight: 500;
		font-size: dpx(16);
		line-height: 130%;
		letter-spacing: 0.02em;
		color: #fff;
		background-color: $color-theme-popup-mark-bgc;

		@include devices {
			left: 0;
			font-size: dpx(14);
		}
	}

	&__title {
		margin-bottom: dpx(29);
		font-family: $font-rubik;
		font-weight: 700;
		font-size: dpx(30);
		line-height: 130%;
		text-align: center;
		letter-spacing: 0.01em;
		text-transform: uppercase;
		color: $color-theme-popup-title;

		@include devices {
			width: dpx(245);
			margin: 0 auto dpx(21);
			font-size: dpx(24);
			text-align: left;
		}
	}

	&__intro {
		margin-bottom: dpx(7);
		font-weight: 500;
		font-size: dpx(24);
		line-height: 130%;
		text-align: center;
		letter-spacing: 0.01em;
		color: $color-theme-popup-some-text;

		b {
			font-weight: 400;
			font-family: $font-rubik;
			color: $color-theme-main-text;
		}

		@include devices {
			margin-bottom: dpx(6);
			font-size: dpx(15);
			text-align: left;
		}
	}

	&__easy-start {
		display: inline-block;
		margin: 0 auto dpx(40);
		padding: dpx(12) dpx(27) dpx(14) dpx(29);
		font-weight: 700;
		font-size: dpx(24);
		line-height: 130%;
		border: 1.5px solid #01b9c5;
		border-radius: dpx(30);

		&-wrap {
			display: flex;
			justify-content: center;
		}

		@include devices {
			width: 100%;
			margin: 0 auto dpx(26);
			padding: dpx(12) dpx(16) dpx(12) dpx(16);
			font-size: dpx(15);
			text-align: center;
		}
	}

	&__accent {
		margin-bottom: dpx(17);
		font-weight: 500;
		font-size: dpx(24);
		line-height: 130%;
		color: $color-theme-popup-some-text;

		@include devices {
			margin-bottom: dpx(11);
			font-size: dpx(15);
		}
	}

	&__offer {
		&-list {
			display: grid;
			column-gap: dpx(20);
			grid-template-columns: 1fr 1fr 1fr;
			margin-bottom: dpx(38);

			&-item {
				position: relative;
				display: flex;
				flex-direction: column;
				flex-grow: 1;
				padding: dpx(24) dpx(20) dpx(20) dpx(24);
				border: 2px solid $color-theme-popup-item-border;
				border-radius: dpx(20);
				cursor: pointer;

				&:hover,
				&.radio-button--active {
					border-color: $color-theme-cell-plan-border;

					&::before {
						content: "";
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background-color: rgba(1, 185, 197, 0.05);
					}
				}

				@include devices {
					padding: dpx(21) dpx(19) dpx(18) dpx(19);
				}
			}

			@include devices {
				row-gap: dpx(6);
				column-gap: unset;
				grid-template-columns: 1fr;

				@include devices {
					row-gap: dpx(3);
					margin-bottom: dpx(18);
				}
			}
		}

		&-top {
			display: grid;
			grid-template-columns: 1fr dpx(50);

			&-right-content {
				display: flex;
				align-items: flex-start;
				justify-content: flex-end;
			}
		}

		&-bottom {
			position: relative;

			@include devices {
				display: flex;
				align-items: flex-end;
				justify-content: space-between;
			}
		}

		&-limit{
			margin-bottom: dpx(14);
			font-family: $font-bebas-neue-cyrillic;
			font-size: dpx(26);
			line-height: 100%;

			@include devices {
				margin-bottom: dpx(21);
				font-size: dpx(24);
			}
		}

		&-price {
			display: inline-block;
			font-weight: 700;
			font-size: dpx(20);
			line-height: 110%;
			color: rgba(45, 50, 66, 0.7);

			@include devices {
				margin-bottom: dpx(9);
			}
		}

		&-separator {
			width: dpx(134);
			height: dpx(1);
			margin: dpx(12) auto dpx(24);
			background-color: $color-theme-popup-separator;
		}

		&-discount {
			position: relative;
			display: inline-block;
			font-weight: 700;
			font-size: dpx(46);
			line-height: 110%;

			@include devices {
				font-size: dpx(44);
			}
		}

		&-percent {
			position: absolute;
			top: dpx(-14);
			right: dpx(-51);
			display: flex;
			align-items: center;
			justify-content: center;
			width: dpx(48);
			height: dpx(48);
			font-weight: 500;
			font-size: dpx(13);
			line-height: 130%;
			color: $color-theme-cell-percent-text;

			&-text {
				position: absolute;
			}

			@include devices {
				top: dpx(-5);
				right: dpx(-45);
				width: dpx(38);
				height: dpx(38);
				font-size: dpx(10);
			}
		}
	}

	&__btn.btn {
		width: dpx(310);
		height: dpx(62);
		margin: auto;
		padding: dpx(12) dpx(12);
		font-size: dpx(20);
		text-align: center;
		text-transform: inherit;
		border-radius: dpx(30);

		&:hover {
			opacity: 0.5;
		}

		@include devices {
			width: 100%;
			height: auto;
			padding: dpx(20) dpx(20) dpx(22);
		}
	}

	.image-in-popup {
		width: dpx(400);
		height: dpx(400);
		vertical-align: top;
	}

	.link-doc-image {
		display: flex;
	}

	.doc-image {
		margin: dpx(20) auto;
	}

	.text--highlight {
		color: $color-theme-popup-highlight-text;
	}

	.icon--highlight {
		color: #fd4d35;
	}
}

body.lock {
	overflow: hidden;
}

.radio-button {
	&__input {
		position: relative;
		top: dpx(3);

		input[type="radio"] {
			position: absolute;
			z-index: -1;
			opacity: 0;

			+ .radio-button__input-custom {
				position: relative;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				width: dpx(22);
				height: dpx(22);
				border: 1.5px solid $color-theme-radio-border;
				border-radius: 50%;

				@include devices {
					width: dpx(20);
					height: dpx(20);
				}
			}

			&:checked + .radio-button__input-custom {
				&::after {
					content: '';
					position: absolute;
					display: inline-block;
					width: dpx(12);
					height: dpx(12);
					border-radius: 50%;
					background-color: $color-theme-radio-border;

					@include devices {
						width: dpx(10);
						height: dpx(10);
					}
				}
			}
		}
	}

	&:hover,
	&.radio-button--active {
		.radio-button__input {
			.radio-button__input-custom {
				border-color: $color-theme-radio-border-hover;
			}

			input[type="radio"]:checked {
				+ .radio-button__input-custom {
					&::after {
						background-color: $color-theme-radio-border-hover;
					}
				}
			}
		}
	}
}

.text--cross {
	position: relative;
	display: inline-block;
	overflow: hidden;

	&::before {
		content:'';
		position: absolute;
		display: block;
		width: auto;
		height: auto;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		background: url(../images/cross.svg) no-repeat;
		background-size: 100% 100%;
	}
}
