.a4consulting {
	min-height: 100vh;
	background-color: $color-theme-main-background;

	.home {
		padding-bottom: dpx(60);
	}

	section.rates {
		@include devices {
			padding-top: dpx(2);
		}
	}

	.section-container {
		max-width: dpx(1098);
		margin: auto;
	}

	h1 {
		margin: 0.7em;
		font-family: $font-rubik;
		font-weight: 700;
		font-size: dpx(40);
		line-height: 110%;
		// letter-spacing: 0.01em;
		letter-spacing: 0.027em;	// in design 0.01em or 1%
		text-transform: uppercase;
		text-align: center;

		@include devices {
			font-size: dpx(24);
			text-align: left;
		}
	}

	.grid-rate {
		display: grid;
		grid-template-columns: 1fr 1fr;
		column-gap: dpx(79);

		@include devices {
			grid-template-columns: 1fr;
		}
	}

	.col--1 {
		display: flex;
		align-items: flex-start;
		padding-top: dpx(56);

		.picture-men {
			position: relative;
			top: dpx(14);
			width: dpx(434);
			height: dpx(715);

			@include devices {
				top: 0;
				width: dpx(266);
				height: dpx(441);
				margin-top: dpx(5);
				margin-right: dpx(12);
			}
		}

		@include devices {
			justify-content: center;
			padding-top: 0;
		}
	}

	.col--2 {
		min-width: dpx(585);

		@include devices {
			min-width: 100%;
			padding: 0 dpx(20);
		}
	}

	.rate-plan {
		&__cells {
			display: grid;
			row-gap: dpx(36);
			column-gap: dpx(12);
			grid-template-columns: 1fr 1fr 1fr;
			padding-top: dpx(69);


			@include devices {
				row-gap: dpx(6);
				column-gap: unset;
				grid-template-columns: 1fr;
				padding-top: dpx(8);
			}
		}

		&__cell {
			&-item {
				position: relative;
				display: flex;
				flex-grow: 1;
				// in design 261px but not use border-width
				height: dpx(265);
				width: dpx(187);
				border: 2px solid $color-theme-rate-plan-border-cell;
				border-radius: 20px;
				cursor: pointer;

				&:nth-child(2n),
				&:nth-child(3n) {

					@include devices {
						margin-left: 0;
					}
				}

				&:nth-child(4n) {
					grid-column: 1 / 4;
					width: 100%;
					height: dpx(129);
					margin-left: 0;

					.rate-plan__cell {
						&-period {
							display: flex;
							align-items: center;
							margin-right: dpx(20);
							// margin-bottom: dpx(13);
							margin-bottom: dpx(-10);
							font-size: dpx(38);
							color: $color-theme-cell-period-4n-text;

							@include devices {
								grid-area: period;
								margin-bottom: 0;
								font-size: dpx(24);
							}
						}

						&-content {
							flex-grow: 1;
							flex-direction: row;
							padding: dpx(26) dpx(50) dpx(25) dpx(29);

							@include devices {
								padding: dpx(34) dpx(23) dpx(20) dpx(22);
							}
						}

						&-nums {
							flex-direction: row;
							align-items: flex-start;
							align-self: flex-start;

							@include devices {
								flex-grow: 0;
							}
						}

						&-cost {
							flex-grow: 0;
						}

						&-discount {
							@include devices {
								font-size: dpx(44);
								letter-spacing: -0.05em;
							}
						}

						&-price {
							position: relative;
							top: dpx(-5);

							@include devices {
								top: 0;
								font-size: dpx(44);
							}
						}

						&-description {
							display: flex;
							align-items: flex-end;
							max-width: dpx(161);
							text-align: left;
						}
					}

					@include devices {
						grid-column: 1;
						height: dpx(144);
						margin-top: 0;
					}
				}

				&:last-child {
					@include devices {
						margin-bottom: 0;
					}
				}

				&:hover {
					border-color: $color-theme-cell-plan-border;

					&::before {
						content: "";
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background-color: rgba(1, 185, 197, 0.05);
					}
				}

				@include devices {
					width: 100%;
					min-width: 100%;
					height: dpx(144);
					// margin-bottom: dpx(6);
				}
			}

			&-content {
				position: relative;
				display: flex;
				flex-direction: column;
				padding: dpx(43) dpx(23) dpx(26) dpx(18);
				overflow: hidden;

				@include devices {
					flex-direction: row;
					padding: dpx(34) dpx(23) dpx(20) dpx(22);
				}
			}

			&-percentage {
				position: absolute;
				top: dpx(-30);
				right: dpx(3);
				z-index: 1;
				// display: flex;
				display: none;
				align-items: center;
				justify-content: center;
				width: dpx(70);
				height: dpx(70);
				padding: 2px;

				&-text {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					font-weight: 500;
					font-size: dpx(18);
					line-height: 130%;
					color: $color-theme-cell-percent-text;

					@include devices {
						font-size: dpx(13);
					}
				}

				@include devices {
					top: dpx(3);
					right: dpx(6);
					width: dpx(51);
					height: dpx(51);
				}
			}

			&-nums {
				flex-grow: 1;
				display: flex;
				align-self: center;
				flex-direction: column;

				@include devices {
					display: grid;
					row-gap: dpx(20);
					column-gap: dpx(5);
					align-self: flex-start;
					grid-template-columns: dpx(118) 1fr;
					grid-template-areas:
						"period cost"
						"description cost";
				}
			}

			&-period {
				margin-bottom: dpx(21);
				font-family: $font-bebas-neue-cyrillic;
				font-weight: 400;
				font-size: dpx(30);
				line-height: 100%;
				color: $color-theme-cell-period-text;

				@include devices {
					grid-area: period;
					margin-bottom: 0;
					font-size: dpx(24);
				}
			}

			&-cost {
				flex-grow: 1;
				display: flex;
				flex-direction: column;

				@include devices {
					grid-area: cost;
				}
			}

			&-price {
				font-weight: 700;
				font-size: dpx(50);
				line-height: 110%;
				letter-spacing: -0.04em;
				color: $color-theme-cell-price-text;

				@include devices {
					font-size: dpx(44);
					letter-spacing: -0.05em;
				}
			}

			&-discount {
				display: none;
				font-weight: 500;
				font-size: dpx(24);
				line-height: 110%;
				text-align: right;
				color: $color-theme-cell-discount-text;

				@include devices {
					font-size: dpx(20);
				}
			}

			&-description {
				font-weight: 500;
				font-size: dpx(16);
				line-height: 130%;
				text-align: center;
				color: $color-theme-cell-description-text;

				@include devices {
					grid-area: description;
					font-size: dpx(14);
					text-align: left;
				}
			}
		}

		&--active-discount {
			&.rate-plan__cells {
				.rate-plan__cell {
					&-percentage {
						display: flex;
					}

					&-price {
						order: 2;
						font-weight: 500;
						font-size: dpx(24);
						line-height: 110%;
						text-align: right;
						color: $color-theme-cell-discount-text;
					}

					&-discount {
						order: 1;
						display: block;
						font-weight: 700;
						font-size: dpx(50);
						line-height: 110%;
						letter-spacing: -0.04em;
						color: $color-theme-cell-price-text;
					}
				}
			}
		}

		&__recommendation {
			margin-top: dpx(11);
			font-weight: 500;
			font-size: dpx(18);
			line-height: 130%;

			@include devices {
				max-width: 90%;
				margin-top: dpx(10);
				font-size: dpx(14);
			}
		}
	}

	.rules {
		&__wrapper {
			max-width: dpx(454);
			margin-top: dpx(26);
			margin-bottom: dpx(50);
			cursor: pointer;

			@include devices {
				max-width: 100%;
				margin-top: dpx(36);
				margin-bottom: dpx(22);
			}
		}

		&__item {
			position: relative;
			display: inline-flex;
			align-items: center;
			cursor: pointer;

			input {
				position: absolute;
				top: dpx(5);
				left: dpx(5);
				width: 1px;
				height: 1px;
				opacity: 0;

				&:checked ~ {
					i::after {
						border-color: $color-white;
					}
				}
			}

			i {
				position: relative;
				display: flex;
				flex-shrink: 0;
				align-items: center;
				justify-content: center;
				width: dpx(24);
				height: dpx(24);
				margin-right: dpx(12);
				border-radius: dpx(5);
				border: 2px solid $color-theme-checkbox-background;
				background: $color-theme-checkbox-background;
				transition: $transition-duration;

				&::before {
					content: "";
					position: absolute;
					left: -1px;
					right: -1px;
					top: -1px;
					bottom: -1px;
					border: 2px solid $color-white;
					border-radius: dpx(5);
					opacity: 0;
					transition: $transition-duration;
				}

				&::after {
					content: "";
					width: dpx(9);
					height: dpx(13);
					margin-bottom: dpx(5);
					border: solid transparent;
					border-width: 0 dpx(2) dpx(2) 0;
					transform: rotate(45deg);
					transition: $transition-duration;
				}
			}
		}

		&__caption {
			font-size: dpx(16);
			line-height: 110%;
			color: $color-theme-checkbox-rules-label;

			a {
				color: $color-theme-checkbox-rules-link;
			}

			@include devices {
				font-size: dpx(16);
			}
		}
	}

	.reminder {
		margin-top: dpx(30);
		font-weight: 400;
		font-size: dpx(14);
		line-height: 130%;
		color: $color-theme-reminder;

		@include devices {
			display: none;
		}
	}
}

.js--time-to-discount {
	.rate-plan__cells {
		.rate-plan__cell {
			&-percentage {
				display: flex;
			}

			&-price {
				order: 2;
				font-weight: 500;
				font-size: dpx(24);
				line-height: 110%;
				text-align: right;
				text-decoration: line-through;
				color: $color-theme-cell-discount-text;

				@include devices {
					font-size: dpx(20);
				}
			}

			&-discount {
				order: 1;
				display: block;
				font-weight: 700;
				font-size: dpx(50);
				line-height: 110%;
				letter-spacing: -0.04em;
				color: $color-theme-cell-price-text;

				@include devices {
					font-size: dpx(44);
					letter-spacing: -0.05em;
				}
			}

			&-item:nth-child(4n) {
				.rate-plan__cell {
					&-period {
						margin-top: dpx(10);
						margin-bottom: 0;

						@include devices {
							margin-top: 0;
						}
					}

					@include devices {
						margin-top: 0;
					}
				}

				.rate-plan__cell-cost {
					position: relative;
					top: dpx(-4);

					@include devices {
						top: dpx(-1);
					}
				}

				.rate-plan__cell-price {
					@include devices {
						font-size: dpx(20);
					}
				}
			}
		}
	}

	.rate-plan__cells--hidden-discount {
		.rate-plan__cell {
			// Заменен на JS. Cкрытие через GSAP
			// &-percentage {
			// 	display: none;
			// }

			&-price {
				// Заменен на JS. Cкрытие через GSAP
				order: 1;
				font-weight: 500;
				font-size: dpx(50);
				line-height: 110%;
				letter-spacing: -0.04em;
				color: $color-theme-cell-price-text;
				text-decoration: auto;

				@include devices {
					font-size: dpx(44);
					letter-spacing: -0.05em;
				}
			}

			// Заменен на JS. Cкрытие через GSAP
			&-discount {
				// 	display: none;
				order: 2;
				font-weight: 500;
				font-size: dpx(24);
			}

			&-item:nth-child(4n) {
				.rate-plan__cell-price {
					top: 0;

					@include devices {
						top: dpx(1);
						font-size: dpx(44);
						letter-spacing: -0.05em;
					}
				}
			}
		}
	}
}

.btn {
	padding: dpx(28) dpx(100);
	width: dpx(281);
	height: dpx(76);
	font-family: $font-rubik;
	font-weight: 500;
	font-size: dpx(20);
	line-height: 100%;
	text-align: center;
	text-transform: uppercase;
	color: #fff;
	border-radius: dpx(60);
	background-color: $color-theme-button-bgc;
	animation: 1.5s linear 0s infinite btn-blink;

	@include devices {
		width: 100%;
	}
}

@keyframes btn-blink {
	50% {
		filter: brightness(1.5) drop-shadow(0px 0px 12px rgba(253, 77, 53, 1));
	}
}
