// COLORS
$color-white: #fff;
$color-charcoal: #2d3242;
$color-charcoal-2: #2f4353;
$color-white-smoke: #f5f7f7;
$color-light-slate-gray: #818798;
$color-tiffany-blue: #01b9c5;
$color-light-gray: #d3d6dd;
$color-light-gray-2: #ced1d8;
$color-dodger-blue: #2d97f9;
$color-dark-grey: #687078;
$color-manatee: #95979f;
$color-portland-orange: #fd4d35;
$color-dark-jungle-green: #12191d;
$color-glitter: #e7eaf1;
$color-davys-grey: #555965;

$color-black: #000;
$color-gray: #eee;
$color-alto: #F1F1F1;
$color-gray-100: #cacaca;
$color-gray-999: #999999;
$color-gray-bg: #F3F3F3;
$color-mountain-meadow: #15b77e;
$color-sys: #1AD871;
$color-orange: #ff7a00;
$color-theme: #4b72ee;
$color-dusty-gray: #999;
$color-pomegranate: #f23f34;
$color-silver: #c7c7c7;
$color-dusty-gray: #999;
$color-pomegranate: #f23f34;
$color-silver: #c7c7c7;
$color-cinnabar: #e34a3e;
$color-coral-red: #ff4540;
$color-crimson: #f63833;
$color-violent-violet: #310e6d;
$color-alabaster: #f8f8f8;
$color-vermilion: #ff4200;
$color-text-light: #222222;

// DESIGN COLORS
$color-theme-main-background: $color-white-smoke;
$color-theme-main-text: $color-charcoal;
$color-theme-timer-number: $color-tiffany-blue;
$color-theme-timer-label: $color-light-slate-gray;
$color-theme-timer-active-discount: $color-portland-orange;
$color-theme-rate-plan-border-cell: $color-light-gray;
$color-theme-checkbox-rules-label: $color-light-slate-gray;
$color-theme-checkbox-background: $color-tiffany-blue;
$color-theme-checkbox-rules-link: $color-dodger-blue;
$color-theme-cell-plan-border: $color-tiffany-blue;
$color-theme-cell-period-text: $color-dark-grey;
$color-theme-cell-period-4n-text: $color-charcoal;
$color-theme-cell-percent-text: $color-white;
$color-theme-cell-price-text: $color-charcoal;
$color-theme-cell-discount-text: $color-manatee;
$color-theme-cell-description-text: $color-charcoal-2;
$color-theme-button-bgc: $color-portland-orange;
$color-theme-reminder: $color-light-slate-gray;
$color-theme-popup-close: $color-light-gray-2;
$color-theme-popup-mark-bgc: $color-tiffany-blue;
$color-theme-popup-highlight-text: $color-tiffany-blue;
$color-theme-popup-title: $color-dark-jungle-green;
$color-theme-popup-some-text: $color-dark-jungle-green;
$color-theme-popup-item-border: $color-glitter;
$color-theme-popup-separator: $color-glitter;
$color-theme-radio-border: $color-davys-grey;
$color-theme-radio-border-hover: $color-tiffany-blue;

// FONTS
// stylelint-disable-next-line
$font-primary: -apple-system, blinkmacsystemfont, "Segoe UI", roboto, oxygen, ubuntu, cantarell, "Open Sans", "Helvetica Neue", sans-serif;
$font-pt-root-ui: "PTRootUI", sans-serif;
$font-bebas-neue: "BebasNeue", sans-serif;
$font-bebas-neue-cyrillic: "BebasNeueCyrillic", sans-serif;
$font-rubik: "Rubik", sans-serif;

// TRANSITIONS
$transition-base: all 0.3s ease;
$transition-duration: 0.3s;

// EASING TRANSITIONS
$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;

// BREAK POINTS
$desktop: 1025;
$desktop-xl: 1800;
$laptop: 1440;
$mobile: 375;
$desktop-vh: 750;
